.navigation-desktop {
  align-items: center;
  background-color: #f5f5f5;
  display: flex;
  flex-direction: column;
  gap: 10px;
  justify-content: center;
  position: relative;
  width: 390px;
}

.navigation-desktop .main-navigation {
  align-items: flex-start;
  align-self: stretch;
  background-color: var(--themewhite);
  display: flex;
  flex: 0 0 auto;
  flex-direction: column;
  position: relative;
  width: 100%;
}

.navigation-desktop .league-rows {
  align-items: flex-start;
  align-self: stretch;
  border-bottom-style: solid;
  border-bottom-width: 1px;
  border-color: #e9ebed;
  display: flex;
  flex-direction: column;
  height: 36px;
  justify-content: center;
  overflow-x: scroll;
  padding: 0px 0px 10px;
  position: relative;
  width: 100%;
}

.navigation-desktop .leauge-row-wrapper {
  align-items: flex-start;
  align-self: stretch;
  display: flex;
  flex: 1;
  flex-grow: 1;
  gap: 15px;
  max-width: 1440px;
  padding: 5.08px 18px;
  position: relative;
  width: 100%;
}

.navigation-desktop .league-row-item {
  align-items: flex-start;
  display: inline-flex;
  flex: 0 0 auto;
  flex-direction: column;
  margin-bottom: -8.16px;
  position: relative;
}

.navigation-desktop .link {
  align-items: flex-start;
  display: inline-flex;
  flex-direction: column;
  height: 24px;
  justify-content: center;
  position: relative;
}

.navigation-desktop .div {
  align-items: flex-start;
  display: inline-flex;
  flex: 0 0 auto;
  flex-direction: column;
  gap: 3px;
  position: relative;
}

.navigation-desktop .content {
  align-items: flex-start;
  display: flex;
  flex: 0 0 auto;
  justify-content: space-between;
  position: relative;
  width: 49px;
}

.navigation-desktop .img {
  height: 13px;
  position: relative;
  width: 16px;
}

.navigation-desktop .text-wrapper {
  color: var(--themeouter-space);
  font-family: var(--theme-inter-bold-12-font-family);
  font-size: var(--theme-inter-bold-12-font-size);
  font-style: var(--theme-inter-bold-12-font-style);
  font-weight: var(--theme-inter-bold-12-font-weight);
  letter-spacing: var(--theme-inter-bold-12-letter-spacing);
  line-height: var(--theme-inter-bold-12-line-height);
  margin-top: -1.00px;
  position: relative;
  width: fit-content;
}

